.row svg {
    animation: jump ease-in-out 5s infinite alternate;
}

#handboy
{
   animation: swing ease-in-out 1.3s infinite alternate;
    transform-origin: 98% 98%;
    transform-box: fill-box;
    
}


#girllight
{
   animation: swing ease-in-out 1.3s infinite alternate;
    transform-origin: 0% 97%;
    transform-box: fill-box;
}

#hairgirl
{
    animation: swinghair ease-in-out 1.3s infinite alternate;
   transform-origin: 60% 0%;
    transform-box: fill-box;
  
}

#zero
{
  transform-origin: bottom;
  transform-box: fill-box;
  animation: flip 1500ms infinite;

}

@keyframes flip {
    30%, 60% {
      transform: rotateY(360deg) scale(1.1) translateY(-20px);
    }
  }

  @keyframes jump {
    0% { transform: rotate(1deg) translateY(20px); }
    100% { transform: rotate(-1deg) translateY(-20px); }
  }

@keyframes swing {
    0% { transform: rotate(10deg); }
    100% { transform: rotate(-10deg); }
}


@keyframes swinghair {
    0% { transform: rotate(6deg); }
    100% { transform: rotate(-6deg); }
}